import React from 'react';


function FitdataDescription() {
	return (
    <>
      <div>
          <Contents />
      </div>
    </>
	);
};

export default FitdataDescription;

function Contents() {
	const title = 'The Fitdata Description Spreadsheet';

	return (
		<>
			<h1>{title}</h1>	

			<hr className='class-1'/>

			
		</>
	);
}	
