import React from 'react';
import SyntaxHighlighter from "react-syntax-highlighter";
import { docco } from 'react-syntax-highlighter/dist/esm/styles/hljs';


function DL_2() {
	return (
    <>
      <div>
          <Contents />
      </div>
    </>
	);
};

export default DL_2;

function Contents() {
	const title = 'CNN 이론/coding';

	return (
		<>
			<h1>{title}</h1>	

			<hr className='class-1'/>
			<h3 className='h5style'><b><u>1. CNN 기초 이론</u></b></h3>

			<h3 className='h5style'><b><u>(1) CNN의 구조</u></b></h3>
				<ul>
					<li>Convolutional layer, Pooling layer, Fully connected layer로 구성</li>
					<ol>
						<li>이미지 데이터를 입력하면 우선 Conv layer에서 필터 연산을 거치게 되고</li>
						<li>활성화함수를 통과한 후,</li>
						<li>Pooling layer에서 pooling된다.</li>
						<li>CNN이나 MLP(Multi-Layer Perceptron)는 지난 입력값의 기억(memory)을 보존하고 있지 않다. </li>
					</ol>
					<li>이 구조가 여러번 반복된다. </li>

					<b><u>1. Convolutional layer</u></b>
					<ul>
						<li>합성곱계층 Convolutional layer(줄여서 Conv layer)는 입력된 이미지의 특징(화소, 구성)을 반영하여 새로 처리된 이미지를 생성하는 계층이다. </li>
						<li>합성곱계층에서는 이미지에 대해 필터연산을 수행하게 되는데 필터의 개수, 크기에 따라 이미지 연산이 달라지기도 한다.</li>
					</ul>
					<b><u>2. Pooling layer</u></b>
					<ul>
						<li>풀링 계층은 합성곱 계층을 통과한 이미지의 대표적인 픽셀만 뽑는 역할을 한다.</li>
						<li>pooling 방법에는 max pooling, global mean pooling이 있는데 일반적으로 CNN에서는 max pooling을 많이 사용한다. </li>
						<li>Max pooling은 n x n 픽셀에서 가장 큰 값만 추출한다.</li>
					</ul>
					<b><u>3. Fully connected layer</u></b>
					<ul>
						<li>완전연결계층에서는 앞에서 모든 처리를 거친 이미지 데이터를 1D array로 변환하여(flatten)</li>
						<li>softmax 함수를 적용할 수 있게끔 변환한다.</li>
					</ul>

			<h3 className='h5style'><b><u>(2) 기본 처리</u></b></h3>
				<ul>
					<li>filter size: 합성곱 계층에서 가장 핵심적인 필터의 크기를 나타내는 것으로, 필터 사이즈에 따라 output image의 사이즈가 달라진다.</li>
					<li>fiter 개수: 필터 개수는 output image의 채널 수를 결정한다.</li>
					<li>padding: 기존 이미지 주변을 특정 값(보통 0)으로 채우는 과정으로, 합성곱 계층을 통과해도 이미지의 사이즈가 감소하는 것을 방지하기 위해 사용한다.</li>
					<li>stride: 합성곱 계층에서 필터가 이동할 때, 처리 후 몇칸을 이동할지를 결정하는 것으로 이 역시 output image의 사이즈를 결정한다.</li>
				</ul>
				
			<hr className='class-1'/>


			<h3 className='h5style'><b><u>2. Simple CNN 코드</u></b></h3>
			<h3 className='h5style'><b><u>(1) load data(손글씨)</u></b></h3>
				<ul>
					<li>train images: 이미지 데이터 600000개, 이미지 크기 28 x 28</li>
					<li>test images: 이미지 데이터 100000개, 이미지 크기 28 x 28</li>
					<li>class 수: 10개(0~9)</li>
					<img src="../images/AI/9.PNG" width={'600'} alt=''/>
				</ul>

			</ul>

			<hr className='class-1'/>
			<h3 className='h5style'><b><u>CNN 손글씨 Code</u></b></h3>	

			<b><u>예제1</u></b>
			<div>
			<SyntaxHighlighter language="python" style={docco} showLineNumbers={true}>
				{`
import tensorflow as tf
import matplotlib.pyplot as plt

# MNIST 데이터 로드
mnist = tf.keras.datasets.mnist

(x_train, y_train),(x_test, y_test) = mnist.load_data()

# imshow를 이용해 MNIST 이미지 세개 미리보기
fig, ax = plt.subplots(nrows = 1, ncols = 3)
for i in range(3):
  ax[i].imshow(x_train[i])

plt.tight_layout()
plt.show()

# 이미지 데이터 표준화
x_train, x_test = x_train / 255.0, x_test / 255.0  #픽셀값 범위(0~225)를 0~1 사이로 조정해 전처리

model = tf.keras.models.Sequential([
  tf.keras.layers.Flatten(input_shape=(28, 28)), #각 이미지는 28x28 픽셀
  tf.keras.layers.Dense(128, activation='relu'),
  tf.keras.layers.Dropout(0.2),
  tf.keras.layers.Dense(10, activation='softmax')
])

model.compile(optimizer='adam',
              loss='sparse_categorical_crossentropy',
              metrics=['accuracy'])

model.fit(x_train, y_train, epochs=5)
model.evaluate(x_test, y_test) #테스트

				`}
    	</SyntaxHighlighter>
			</div>

			<ul>
				<li>tf.keras.layers.Flatten : input_shape인 28*28(2차원 배열)의 이미지 포맷을 28*28=784 픽셀의 1차원 배열로 변환함. </li>
				<li>tf.keras.layers.Dense : 이 층은 밀집 연결(densely-connected)층 또는 완전 연결(fully-connected)층 이라고 부른다. 
					위 코드의 첫 번째 Dense 층은 128개의 노드, 두번째 Dense 층은 10개의 노드를 갖는다. </li>
				<li>tf.keras.layers.Dropout(0.2) : overfitting을 방지하기 위한 방법 중 하나로 입력층과 은닉층의 총 노드 수의 0.2%를 생략 하고 학습함. 
					주로 0.2~0.5를 많이 사용함.</li>
				<li>활성화 함수 'softmax'는 주로 클래스 값이 3개 이상인 데이터에 마지막 Dense(출력값)에 주로 쓰인다. 
					위 코드를 보면 총 10개의 확률을 반환(전체 확률의 합은 1)하며, 각 노드는 현재 이미지가 10개의 클래스 중 하나에 속할 확률을 출력한다.</li>
				<li>model.compile : 모델을 훈련하기 전에 필요한 몇 가지 설정이  추가된다.</li>
				<li>optimizer : 데이터와 손실 함수를 바탕으로 모델의 업데이트 방법을 결정</li>
				<li>loss : Loss function. 훈련하는 동안 모델의 오차를 측정. 모델의 학습이 올바른 방향으로 향하도록 이 함수를 최소화해야함.</li>
				<li>metrics : 훈련 단계와 테스트 단계를 모니터링하기 위한 지표 설정.  accuracy(정확도), MSE, MAE (오차의평균) 등이 있음.</li>
				<li>epochs : 지금까지의 과정 반복 횟수</li>
			</ul>


			<b><u>예제2</u></b>
			<div>
			<SyntaxHighlighter language="python" style={docco} showLineNumbers={true}>
				{`
import numpy as np
import pandas as pd
import tensorflow as tf
from tensorflow import keras

from sklearn.datasets import fetch_openml
mnist = fetch_openml('mnist_784',version=1,cache=True)

from tensorflow.keras.utils import to_categorical
X = (mnist.data.values/255).reshape(len(mnist.data),28,28,1)
y = to_categorical(mnist.target.astype(np.int),10)

from sklearn.model_selection import train_test_split
X_train,X_test,y_train,y_test = train_test_split(X,y,test_size=0.3,random_state=1)


from keras.models import Sequential
from keras.layers import Dense,Flatten,Dropout
from keras.layers.convolutional import Conv2D,MaxPooling2D

model = Sequential()
model.add(Conv2D(filters=32,kernel_size=(3,3),strides=(1,1),padding='Same',activation='relu',input_shape=(28,28,1)))
model.add(Conv2D(filters=32,kernel_size=(3,3),strides=(1,1),padding='Same',activation='relu'))
model.add(MaxPooling2D(pool_size=(2,2)))
model.add(Conv2D(filters=64, kernel_size=(3,3),strides=(1,1),padding='Same',activation='relu'))
model.add(Conv2D(filters=64, kernel_size=(3,3),strides=(1,1),padding='Same',activation='relu'))
model.add(MaxPooling2D(pool_size=(2,2)))
model.add(Flatten())
model.add(Dense(units=128,activation='relu'))
model.add(Dropout(0.25))
model.add(Dense(units=10,activation='softmax'))

model.compile(loss='categorical_crossentropy',optimizer='adam',metrics=['accuracy'])
model.summary()

hist = model.fit(X_train,y_train,epochs =5 , batch_size=1000,verbose=2)

loss,acc = model.evaluate(X_test,y_test)
print('손실값:',round(loss,3))
print('정확도:',round(acc,3))

import matplotlib.pyplot as plt
plt.figure(figsize=(20,10))

plt.subplot(1,2,1)
plt.plot(hist.history['loss'],color='blue',linestyle='--',label='train')
plt.plot(hist.history['val_loss'],color='red',label='validation')
plt.xlabel('epoch',fontsize=15)
plt.ylabel('loss',fontsize=15)
plt.title('LOSS',fontsize=18)
plt.grid(linestyle='--')
plt.legend(fontsize=15,loc="best") 
plt.ylim(0,0.2)


plt.subplot(1,2,2)
plt.plot(hist.history['accuracy'],color='blue',linestyle='--',label='train')
plt.plot(hist.history['val_accuracy'],color='red',label='validation')
plt.xlabel('epoch',fontsize=15)
plt.ylabel('accuracy',fontsize=15)
plt.title('ACCURACY',fontsize=18)
plt.grid(linestyle='--')
plt.legend(fontsize=15,loc="best") 
plt.ylim(0.9,1)
plt.show()


				`}
    	</SyntaxHighlighter>
			</div>

			<ul>
				<li>tensorflow와 keras를 import 하고 mnist data를 fetch_openml에서 불러온다. </li>
				<li>mnist.data에 feature가, mnist.target에 정답 레이블이 들어 있다.</li>
				<li>mnist.data는 (70000, 784)로, 70000개의 흑백의 손글씨 이미지가 들어있고, 
					한 이미지는 28*28의 픽셀로 이루어져 있는데 28*28 = 784개의 픽셀이 한 행에 일렬로 놓인다. 
					각 픽셀은 0부터 255까지의 정수로 흑백을 나타낸다.</li>
				<li>mnist.data.values/255로 모든 픽셀이 0~1사이의 값을 갖도록 정규화를 해주었다.</li>
				<li>CNN에 넣는 이미지 데이터는 4차원이어야 한다. (행의 개수, width, height, channel)이고, 
					mnist data는 28*28 픽셀로 이루어져있으므로 width = 28, height = 28으로, 
					흑백 이미지이므로  channel = 1로 넣어준다.</li>
				<li>mnist.target은 0~9까지의 정답 라벨이 들어있는데, str 형식으로 되어있기 때문에 astype(np.int)로 정수 형태로 바꿔주고, 
					one hot encoding을 위해 to_categorical 함수를 적용해준다.</li>
					<img src="../images/AI/84.PNG" width={'600'} alt=''/>
			</ul>
			




		</>
	);
}	
