import React from 'react';
import SyntaxHighlighter from "react-syntax-highlighter";
import { docco } from 'react-syntax-highlighter/dist/esm/styles/hljs';


function ML() {
	return (
    <>
      <div>
          <Contents />
      </div>
    </>
	);
};

export default ML;

function Contents() {
	const title = 'Machine Learning';

	return (
		<>
			<h1>{title}</h1>	

			<hr className='class-1'/>
			<h3 className='h5style'><b><u>머신러닝(ML, machine learning)</u></b></h3>
			
			<ul>
				<li><b><u>머신 러닝이란(What is Machine Learning?)</u></b></li>
					<ul>
						<li>기존의 프로그래밍 방식을 통해서는 해결하기 어려운 문제</li>
						<li>2017년에 있었던 DGIST의 딥 러닝 경진대회의 문제</li>
						<li><u>주어진 사진으로부터 고양이 사진인지 강아지 사진인지 판별</u></li>
						<li>사진을 보고 고양이, 강아지 판단는 사람에게는 쉬운 일입니다. </li>
						<li>사진는 각도, 조명, 고양이의 자세에 따라서 천차만별이라 사진으로부터 공통된 명확한 특징을 잡아내는 것은 쉽지 않다. </li>
						<img src="../images/AI/31.PNG" width={'200'} alt=''/>
						<img src="../images/AI/32.PNG" width={'200'} alt=''/>
						<img src="../images/AI/33.PNG" width={'200'} alt=''/>
						<li>기존: 특징으로 판별하는 프로그램한다., 물론 특징은 사람이 찾아야 한다.</li>
						<img src="../images/AI/34.PNG" width={'600'} alt=''/>
						<li>머신러닝: 기계가 스스로 데이터로부터 규칙성을 찾아 낸다.</li>
						<li></li>
						<li></li>
					</ul>

				<li>
				<hr className='class-1'/>
					<b><u>선형 회귀(Linear Regression)</u></b>
					<ul>
						<li>공부시간:성적, 운동량:몸무게 등 </li>
						<li>즉, 어떤값이 변하면(변수) 특정 값이 영향을 받는다.</li>
						<li>y = ax + b</li>
						<li>종속변수(y) = 기울기(a) * 독립변수(x) + 절편(b)</li>
						<li>y = wx + b</li>
						<li>종속변수(y) = 가중치(weight) * 독립변수(x) + 편향(bias)</li>
						<li>하나의 직선 이고, w, b 값에 따라서 x, y가 표현하는 직선은 무궁무진해집니다.</li>
					</ul>
				</li>

				<li>
					<b><u>다중 선형 회귀 분석(Multiple Linear Regression Analysis)</u></b>
					<ul>
						<li>많은 독립변수에 따라 변하는 종속변수인 경우</li>
						<li>y = w<sub>1</sub>x<sub>1</sub> + w<sub>2</sub>x<sub>2</sub> + ... + w<sub>n</sub>x<sub>n</sub> + b</li>
						<li></li>
					</ul>
				</li>

				<li>
					<b><u>가설(Hypothesis)</u></b><br/>
					<img src="../images/AI/35.PNG" width={'200'} alt=''/>
					<img src="../images/AI/36.PNG" width={'400'} alt=''/>
					<img src="../images/AI/37.PNG" width={'400'} alt=''/>
					<ul>
						<li>x, y의 관계를 유추하기 위해서 수학적으로 식을 <b>가설(Hypothesis)</b>이라 한다.</li>
						<li><img src="../images/AI/38.PNG" width={'150'} alt=''/></li>
						<li>비용 함수(Cost function) : 평균 제곱 오차(MSE)</li>
						<img src="../images/AI/45.PNG" width={'400'} alt=''/>
					</ul>
				</li>
				<hr className='class-1'/>
				<li>
					<b><u>로지스틱 회귀(Logsitic Regression)</u></b>
					<ul>
						<li>두 개의 선택지 중에서 정답을 고르는 문제</li>
						<li>이진 분류 문제는 직선으로 표현하는 것이 적절하지 않습니다.</li>
						
						<img src="../images/AI/39.PNG" width={'200'} alt=''/>
						<img src="../images/AI/40.PNG" width={'400'} alt=''/>
						<li>이러한 점들을 표현하는 그래프는 알파벳의 S자 형태로 표현됩니다. </li>
						<li>직선을 표현하는 함수가 아니라 S자 형태로 표현할 수 있는 함수가 필요합니다. </li>
						<li>레이블에 해당하는 y 가 0 또는 1이라는 두 가지 값만을 가지므로, 이 문제를 풀기 위해서 예측값은 0과 1사이의 값을 가지도록 합니다.</li>
						<li>출력이 0과 1사이의 값을 가지면서 S자 형태로 그려지는 함수로 시그모이드 함수(Sigmoid function)가 있습니다.</li>
						<li></li>
						<li></li>
						<li></li>
						<li></li>
					</ul>
				</li>

				<li>
					<b><u>시그모이드 함수(Sigmoid function)</u></b><br/>
					<img src="../images/AI/42.PNG" width={'400'} alt=''/>
					<img src="../images/AI/43.PNG" width={'400'} alt=''/>
					<ul>
						<li>x, y의 관계를 유추하기 위해서 수학적으로 식을 <b>가설(Hypothesis)</b>이라 한다.</li>
						<li><img src="../images/AI/41.PNG" width={'500'} alt=''/></li>
						<li>w, b에 따라 그림이 달라진다.</li>

						<li>비용 함수(Cost function) : 크로스 엔트로피(Cross Entropy)함수</li>
						<img src="../images/AI/44.PNG" width={'600'} alt=''/>
					</ul>
				</li>

			</ul>
			<hr className='class-1'/>
			<ul>
				<li>지도 학습(Supervised Learning): 정답이 있는 데이터(분류(Classification), 회귀(Regression))</li>
				<li>비지도 학습(Unsupervised Learning): 정답이 없는 데이터(군집화)</li>
				<li>준지도 학습(semi supervised): 정답이 존재하나 정답이 있는 데이터를 구하기 힘들 때</li>
				<li>강화 학습(Reinforcement Learning): 행동을 취하고 이로부터 어떤 보상을 얻으면서 학습을 진행</li>
				<li></li>
				<li></li>
			</ul>


		</>
	);
}	
