import React from 'react';
import SyntaxHighlighter from "react-syntax-highlighter";
import { docco } from 'react-syntax-highlighter/dist/esm/styles/hljs';


function DL_3() {
	return (
    <>
      <div>
          <Contents />
      </div>
    </>
	);
};

export default DL_3;

function Contents() {
	const title = 'RNN 이론/coding';

	return (
		<>
			<h1>{title}</h1>	

			<hr className='class-1'/>
			<h3 className='h5style'><b><u>1. RNN 기초 이론</u></b></h3>
				<ol>
					<li>기존의 인공 신경망의 은닉층은 전후 맥락이 고려되지 않고 단순히 뉴런만 배치되어있는 구조이다.</li>
					<li>전통적 뉴럴 네트워크는 이전에 일어난 사건을 바탕으로 나중을 생각하지 못한다</li>
					<li>RNN은 이 문제를 해결하고자 하는 모델이고 스스로를 반복하면서 이전 단계에서 얻은 정보가 지속되도록 한다.</li>
					<li>순서가 중요한 sequence data에 사용하는 모델 구조로 기존 인공신경망에서 순환성을 추가하였다.</li>
					<li>RNN은 순환형 구조로 인해 지난 입력값에 대한 기억(memory)을 가지고 있다</li>
					<li></li>
				</ol>
			<h3 className='h5style'><b><u>(1) RNN의 구조</u></b></h3>
				<ul>
					<li>RNN(Recurrent Neural Network) 순환 신경망 모델은 입력과 출력을 Sequence 단위로 처리하는 가장 기본적인 시퀀스 모델입니다.</li>
					<img src="../images/AI/85.PNG" width={'600'} alt=''/>
					<img src="../images/AI/28.PNG" width={'600'} alt=''/>
					<ol>
						<li>은익층의 RNN셀(초록)은 앞단계의 출력을 입력으로 사용한다.</li>
						<li>input xt를 받아서 ht를 내보내고, ht는 다음 단계의 network로 전달되어, 이전 단계의 정보를 받는것이다.</li>
						<li>RNN의 기본 구조는 아래위 그림과 같다. 녹색 박스는 히든 state를 의미한다.</li>
						<li>빨간 박스는 input 𝑥, 파란 박스는 output 𝑦이다. </li>
						<li>재 상태의 히든 state ℎ𝑡는 직전 시점의 히든 state ℎ𝑡−1를 받아 갱신됩니다. </li>
						<li>hidden state의 활성함수는 비선형함수인 하이퍼볼릭 탄젠트 함수 (tanh)이다. </li>
						<li>새로운값 = 활성화함수 tanh(가중치 x 이전값 + 가중치 x 입력값 )</li>
					</ol>

					<hr className='class-1'/>
					<h3 className='h5style'><b><u>(2) RNN의 종류</u></b></h3>
					<ul>
						<li>one to one : 사진 한장을 받아 사진의 설명 문장 출력</li>
						<li>many to one : 이메일 판정</li>
						<li>one to many : </li>
						<li></li>
						<li>many to many : 문장을 입력으로 받아서 여러 개의 단어로 구성된 문장을 반환한다.(번역)</li>
					</ul>

					<b><u>1. 바닐라 RNN</u></b>
					<ul>
						<li>RNN의 가장 단순한 형태를 바닐라 RNN이라고 말합니다.</li>
						<li>가장 중요한 정보가 앞 쪽의 시점에 위치할 경우 바닐라 RNN의 경우 앞 쪽의 중요한 정보가 뒤로 오면서 정보량의 손실이 일어나게 됩니다.</li>
						<li></li>
						<li></li>
						<li></li>
					</ul>
					<b><u>2. LSTM(장단기 메모리)</u></b>
					<ul>
						<li></li>
						<li></li>
					</ul>
					<b><u>3. GRU( 게이트 순환 유닛)</u></b>
					<ul>
						<li></li>
						<li></li>
					</ul>

			<h3 className='h5style'><b><u>(2) 기본 처리</u></b></h3>
				<ul>
						<li></li>
						<li></li>
				</ul>
				
			</ul>

			<hr className='class-1'/>
			<h3 className='h5style'><b><u>tf.keras.layers.Dense</u></b></h3>	

			<b><u>예제1</u></b>
			<div>
			<SyntaxHighlighter language="python" style={docco} showLineNumbers={true}>
				{`
import tensorflow as tf
import matplotlib.pyplot as plt

# MNIST 데이터 로드
mnist = tf.keras.datasets.mnist

(x_train, y_train),(x_test, y_test) = mnist.load_data()

# imshow를 이용해 MNIST 이미지 세개 미리보기
fig, ax = plt.subplots(nrows = 1, ncols = 3)
for i in range(3):
  ax[i].imshow(x_train[i])

plt.tight_layout()
plt.show()

# 이미지 데이터 표준화
x_train, x_test = x_train / 255.0, x_test / 255.0  #픽셀값 범위(0~225)를 0~1 사이로 조정해 전처리

model = tf.keras.models.Sequential([
  tf.keras.layers.Flatten(input_shape=(28, 28)), #각 이미지는 28x28 픽셀
  tf.keras.layers.Dense(128, activation='relu'),
  tf.keras.layers.Dropout(0.2),
  tf.keras.layers.Dense(10, activation='softmax')
])

model.compile(optimizer='adam',
              loss='sparse_categorical_crossentropy',
              metrics=['accuracy'])

model.fit(x_train, y_train, epochs=5)
model.evaluate(x_test, y_test) #테스트

				`}
    	</SyntaxHighlighter>
			</div>

			<ul>
				<li>tf.keras.layers.Flatten : input_shape인 28*28(2차원 배열)의 이미지 포맷을 28*28=784 픽셀의 1차원 배열로 변환함. </li>
				<li>tf.keras.layers.Dense : 이 층은 밀집 연결(densely-connected)층 또는 완전 연결(fully-connected)층 이라고 부른다. 
					위 코드의 첫 번째 Dense 층은 128개의 노드, 두번째 Dense 층은 10개의 노드를 갖는다. </li>
				<li>tf.keras.layers.Dropout(0.2) : overfitting을 방지하기 위한 방법 중 하나로 입력층과 은닉층의 총 노드 수의 0.2%를 생략 하고 학습함. 
					주로 0.2~0.5를 많이 사용함.</li>
				<li>활성화 함수 'softmax'는 주로 클래스 값이 3개 이상인 데이터에 마지막 Dense(출력값)에 주로 쓰인다. 
					위 코드를 보면 총 10개의 확률을 반환(전체 확률의 합은 1)하며, 각 노드는 현재 이미지가 10개의 클래스 중 하나에 속할 확률을 출력한다.</li>
				<li>model.compile : 모델을 훈련하기 전에 필요한 몇 가지 설정이  추가된다.</li>
				<li>optimizer : 데이터와 손실 함수를 바탕으로 모델의 업데이트 방법을 결정</li>
				<li>loss : Loss function. 훈련하는 동안 모델의 오차를 측정. 모델의 학습이 올바른 방향으로 향하도록 이 함수를 최소화해야함.</li>
				<li>metrics : 훈련 단계와 테스트 단계를 모니터링하기 위한 지표 설정.  accuracy(정확도), MSE, MAE (오차의평균) 등이 있음.</li>
				<li>epochs : 지금까지의 과정 반복 횟수</li>
			</ul>


		</>
	);
}	
