import React from 'react';

function 계산화학() {
	return (
    <>
      <div>
          <Contents />
      </div>
    </>
	);
};

export default 계산화학;

function Contents() {
  const title = '촉매 계산과학(DFT, VESTA)';
  return (
		<>
    <h1>{title}</h1>
      <h3>++++ 촉매 계산과학(DFT, VESTA) ++++</h3>

        <h3><b><u>A. VESTA</u></b></h3>	
        <ol>
          <li><a href="https://jp-minerals.org/vesta/en/download.html" target='_blank'  rel="noopener noreferrer" >VESTA Home page(download)</a></li>
          <li><a href="https://next-gen.materialsproject.org/materials" target='_blank'  rel="noopener noreferrer" >**** Materials Explorer *****</a></li>
          <li><a href="http://rruff.geo.arizona.edu/AMS/periodictable.php" target='_blank'  rel="noopener noreferrer" >American Mineralogist Crystal Structure Database(미국 광물결정 구조데이터베이스)</a></li>
          <li><a href="http://crystallography.net/cod/">Crystallography Open Database</a></li>


        </ol>	

		</>
	);
}	

