import React, {Fragment} from 'react';

function PDOS() {
  return (
    <>
      <div>
          <Contents />
      </div>
    </>
  );
}
export default PDOS;

function Contents() {
	
	const title = 'PDOS(photon density of states)';

  return (
    <>
      <h1>{title}</h1>

    </>
  );
}	

