import React from 'react';


function ParamDescription() {
	return (
    <>
      <div>
          <Contents />
      </div>
    </>
	);
};

export default ParamDescription;

function Contents() {
	const title = 'The Param Description Spreadsheet';

	return (
		<>
			<h1>{title}</h1>	

			<hr className='class-1'/>

		</>
	);
}	
