import React from 'react';
import SyntaxHighlighter from "react-syntax-highlighter";
import { docco } from 'react-syntax-highlighter/dist/esm/styles/hljs';


function Intro() {
	return (
    <>
      <div>
          <Contents />
      </div>
    </>
	);
};

export default Intro;

function Contents() {
	const title = '이론, 용어';

	return (
		<>
			<h1>{title}</h1>	

			<hr className='class-1'/>
			<h3 className='h5style'><b><u>H(x) = Wx + b</u></b></h3>	
			<ul>
				<li>input, 입력층, 수용층</li>
				<li>연합층</li>
				<li>output, 출력층, 반응층</li>
				<li>Weight, 가중치, 기울기</li>
				<li>Hypothesis(가설)</li>
				<li>Bias, 편향, 절편값</li>
				<li></li>
				<li><li><img src="../images/AI/6.PNG" width={'400'} alt=''/><br/></li></li>
			</ul>

			<hr className='class-1'/>
			<h3 className='h5style'><b><u>퍼셉트론(perceptron)</u></b></h3>	
			<ul>
				<li><img src="../images/AI/7.PNG" width={'400'} alt=''/><br/></li>
				<li></li>
				<li></li>
				<li></li>
				<li></li>
				<li></li>
			</ul>		
				
			<hr className='class-1'/>
			<h3 className='h5style'><b><u>활성화 함수(Activation Function)</u></b></h3>	
			<ul>
				<li><img src="../images/AI/7.PNG" width={'400'} alt=''/><br/></li>
				<li></li>
				<li></li>
				<li></li>
				<li>회귀 문제: 일반적으로 활성화 함수를 사용하지 않는다. 예측값을 그대로 사용하면 되기 때문.</li>
				<li>시그모이드(Sigmoid) 함수, 렐루(ReLU) 함수, 항등 함수, 소프트맥스 등 여러 종류</li>
				<li><img src="../images/AI/8.PNG" width={'500'} alt=''/><br/></li>
			</ul>

			<hr className='class-1'/>
			<h3 className='h5style'><b><u>Cost function(=Loss function)</u></b></h3>	
			<ul>
				<li> 실제 데이터와 우리의 가설이 나타내는 데이터의 거리를</li>
				<li><img src="../images/AI/4.PNG" width={'300'} alt=''/><br/></li>
				<li><img src="../images/AI/5.PNG" width={'400'} alt=''/><br/></li>
				<li></li>
				<li></li>
			</ul>
						
			<hr className='class-1'/>
			<h3 className='h5style'><b><u>데이터마이닝(data mining)</u></b></h3>	
			<ul>
				<li>자료채굴</li>
				<li>대규모로 저장된 데이터안에서 체계적이고 자동적으로 통계적규칙이나 짜임을 분석하여, 가치있는 정보를 빼내는 과정이다.</li>
				<li>데이터에서 어떤 의미있는 값을 알아내는 것(knowledge-discovery in databases)라고도 한다.</li>
				<li></li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-1'/>
			<h3 className='h5style'><b><u>Classification과 Regression</u></b></h3>	
			<ul>
				<li>Classification(분류)과 Regression(회귀) 알고리즘은 supervised learning(지도 학습) 알고리즘</li>
				<li>레이블이 지정된 데이터셋 이용</li>
				<li>
					regression(회귀)
					<ul>
						<li>continuous한 value를 예측</li>
						<li>종속변수와 독립변수 사이의 상관관계를 찾습니다. </li>
						<li>입력 변수 "x"를 연속 출력 변수 "y"에 매핑할 수 있는 매핑 함수를 찾는 알고리즘</li>
						<li></li>
					</ul>
				</li>
				
				<li>
					classification
					<ul>
						<li>discrete한 value를 예측/분류</li>
						<li>매개변수를 기반으로 데이터 세트를 클래스로 나누는 데 도움이 되는 함수를 찾는 알고리즘</li>
						<li> "x" 입력을 "y" 이산 출력에 매핑하는 매핑 함수</li>
						<li>이진 분류 (Binary classification): 예/아니오로 구분</li>
						<li>다중 분류 (Multi-class classification):입력 text가 어느 언어인지 분류</li>
					</ul>
				</li>
				<li></li>
				<li><img src="../images/AI/11.PNG" width={'400'} alt=''/><br/></li>
			</ul>

			<hr className='class-1'/>
			<h3 className='h5style'><b><u>tf.keras.layers.Dense</u></b></h3>	

			<b><u>예제1</u></b>
			<div>
			<SyntaxHighlighter language="python" style={docco} showLineNumbers={true}>
				{`
model = tf.keras.models.Sequential()
model.add(tf.keras.Input(shape=(4,)))
model.add(tf.keras.layers.Dense(16, activation='relu'))
model.add(tf.keras.layers.Dense(32))

print(model.output_shape)

(None, 32)
				`}
    	</SyntaxHighlighter>
			</div>

			<ul>
				<li>tf.keras.Sequential()는 여러 층을 순서대로 쌓아서 신경망 모델을 구성합니다.</li>
				<li>tf.keras.Sequential()는 여러 층을 순서대로 쌓아서 신경망 모델을 구성합니다.</li>
				<li>데이터에서 어떤 의미있는 값을 알아내는 것(knowledge-discovery in databases)라고도 한다.</li>
				<li>예제에서는 입력층과 두 개의 Dense 층을 이용해서 하나의 신경망 모델을 구성했습니다.</li>
				<li>첫번째 인자는 출력의 개수입니다.</li>
				<li>activation은 활성화 함수 (Activation function)를 지정합니다.</li>
			</ul>

			<b><u>예제2</u></b>
			<SyntaxHighlighter language="python" style={docco} showLineNumbers={true}>
				{`
import tensorflow as tf
model = tf.keras.Sequential()
model.add(tf.keras.Input(shape=(4,)))
model.add(tf.keras.layers.Dense(16, activation='relu', use_bias=True))
model.add(tf.keras.layers.Dense(64, activation='relu', use_bias=False))
model.add(tf.keras.layers.Dense(32))

weights = model.get_weights()

print(len(weights))

## Weight matrix
print(weights[0])
print(weights[0].shape)
print(weights[2])
print(weights[2].shape)
print(weights[3])
print(weights[3].shape)

## Bias
print(weights[1])
print(weights[1].shape)
print(weights[4])
print(weights[4].shape)

(None, 32)
				`}
    	</SyntaxHighlighter>
			<ul>
				<li>use_bias는 신경망에서 bias 파라미터를 사용할지 여부를 지정합니다.</li>
				<li>get_weights() 메서드를 사용해서 모델의 weight를 얻어보면,</li>
				<li>weights[0], weights[2], weights[3]은 각각 (4, 16), (16, 64), (64, 32) 형태의 weight matrix이고,</li>
				<li>weights[1], weights[4]는 (16,), (32,) 형태의 bias 값임을 알 수 있습니다.</li>
			</ul>

			<hr className='class-1'/>
			<h3 className='h5style'><b><u>[Tensorflow] 간단한 인공지능 모델 생성 및 확인</u></b></h3>	

			<b><u>전체 코드</u></b>
			<SyntaxHighlighter language="python" style={docco} showLineNumbers={true}>
				{`
import tensorflow as tf

mnist = tf.keras.datasets.mnist

(x_train, y_train), (x_test, y_test) = mnist.load_data()
x_train, x_test = x_train / 255.0, x_test / 255.0

model = tf.keras.models.Sequential([
  tf.keras.layers.Flatten(input_shape=(28, 28)),
  tf.keras.layers.Dense(128, activation='relu'),
  tf.keras.layers.Dropout(0.2),
  tf.keras.layers.Dense(10, activation='softmax')
])
# model.summary()

model.compile(optimizer='adam',
              loss='sparse_categorical_crossentropy',
              metrics=['accuracy'])

model.fit(x_train, y_train, epochs=5)


scores  = model.evaluate(x_test,  y_test, verbose=2)
print("정확도 : %.2f %%" % (scores[1]*100))

			`}
			</SyntaxHighlighter>
			<b><u>코드 설명</u></b>
			<ul>
				<li>2. 위 코드는 텐서플로우 라이브러리를 불러오고, 라이브러리 이름을 tf라고 칭한다는 뜻입니다.</li>
				<li>4. tensorflow 에서 제공하는 데이터 셋인 mnist 를 불러온다, 손글씨 숫자에 관한 이미지 데이터 셋</li>
				<li>
					6. 위 코드는 데이터에서 객체의 데이터와 라벨값을 불러오는 코드
					<ul>
						<li>x_train : 손글씨 숫자 '이미지' 대입</li>
						<li>y_train : 이미지가 의미하는 숫자 대입(라벨값)</li>
						<li>x_test : 손글씨 숫자 '이미지' 대입</li>
						<li>y_test : 이미지가 의미하는 숫자 대입(라벨값)</li>
						<li><li><img src="../images/AI/9.PNG" width={'400'} alt=''/><br/></li></li>
					</ul>

				</li>
				<li>7. 위 코드로 데이터 값을 0–1로 정규화합니다.(흑백으로 나누는 거죠)</li>
				<li>
					9. 모델을 구성
					<ul>
						<li>Sequential : 뉴럴네트워크 레이어를 생성</li>
						<li>Flatten(input_shape=(28, 28)) : 다차원 배열을 일차배열로 만들어 주는 것, 28 x 28 배열을 입력으로 받아 1차원 배열로 변환</li>
						<li>layers.Dense(128, activation='relu'), : 히든레이어의 노드개수는 128개, 활성화 함수는 relu
							이 레이어에서 입력은 가중치 값과 곱해지고 더해진다음, 학습이 진행되며 이 가중치 값이 수정되어 지며 정답을 찾아가는 것이다.
						</li>
						<li>layers.Dropout(0.2), : 오버피팅 방지한 것으로 이전레이어의 출력을 20% 0으로 만듭니다.</li>
						<li>layers.Dense(10, activation='softmax') : 숫자는 0부터 9까지 총 10개의 라벨값을 가지므로
							모델의 출력은 10개입니다.
							활성화 함수는 softmax로 다중 클래스 분류 문제의 출력층에서 주로 쓰이는 모델입니다.

							이 모델을 그림으로 표현하면 다음과 같습니다.</li>
						<li><img src="../images/AI/10.PNG" width={'600'} alt=''/><br/></li>
					</ul>
				</li>
				<li>17. 모델을 컴파일 해줍니다</li>
				<li>21. Mnist 데이터 셋에서 훈련데이터 셋을 사용하고
					epochs를 5로 설정하여 모델을 5번 반복하여 훈련합니다.</li>
			</ul>
			<b><u>예제2</u></b>


		</>
	);
}	
