import React from 'react';


function ch1_1() {
	return (
    <>
      <div>
          <Contents />
      </div>
    </>
	);
};

export default ch1_1;

function Contents() {
	const title = '1. Introduction';

	return (
		<>
			<h1>{title}</h1>	

			<hr className='class-1'/>
			<h3 className='h5style'><b><u>1.1. Neuro Computing이란 무엇입니까?</u></b></h3>	
			<img src="../../images/neurocomputing/ch1/0.PNG" width={'500'} alt=''/><br/>
			<p>먼저 Artificial Intelligence(AI), Machine Learning(ML), Deep Learning(DL), Neurocomputing 의 차이점에 대해 논의해 보겠습니다. 
				오늘날 이러한 용어는 거의 같은 의미로 사용되지만 역사적, 방법론적 차이가 있습니다.</p>
			<p>인공 지능이라는 용어는 1956년 다트머스 인공 지능 여름 연구 프로젝트에서 존 매카시(John McCarthy)에 의해 만들어졌습니다 .</p>

			<p><b>머신러닝 (ML)</b>은 예시 (데이터 기반 AI)를 통한 학습 에 중점을 두는 AI의 한 분야입니다 .</p>
			<p>빅 데이터, 데이터 과학, 운영 연구, 패턴 인식이라고도 합니다. ML 알고리즘에는 다음이 포함됩니다.</p>
			<ul>
				<li>인공 신경망(다층 퍼셉트론)</li>
				<li>통계분석(베이지안 모델링, PCA)</li>
				<li>클러스터링 알고리즘(k-평균, GMM, 스펙트럼 클러스터링)</li>
				<li>지원 벡터 머신</li>
				<li>의사결정 트리, 랜덤 포레스트</li>
			</ul>

			<p><b>딥러닝(Deep Learning)</b>은 최근 인공 신경망을 리브랜딩한 것입니다. 
			고도로 비선형적인 신경망을 사용하여 데이터의 높은 수준의 표현을 학습하는 데 중점을 둡니다. 
			다음을 포함하여 많은 아키텍처가 개발되었습니다.</p>

			<ul>
				<li>심층 신경망(DNN)</li>
				<li>CNN(컨벌루션 신경망)</li>
				<li>순환 신경망(RNN)</li>
				<li>생성 모델(GAN, VAE)</li>
				<li>심층 강화 학습(DQN, PPO, AlphaGo)</li>
				<li>트랜스포머</li>
				<li>그래프 신경망</li>
			</ul>

			<p><b>신경컴퓨팅(NeuroComputing)</b>은 계산 신경과학과 인공 신경망(딥 러닝)의 교차점에 있습니다.</p>
			<p>인간 인지의 기본 메커니즘을 인공 지능으로 가져오는 것을 목표로 합니다. </p>
			<ul>
				<li>딥 러닝에 중점을 두고</li>
				<li>보다 생물학적 신경망이 더 가까운 AI 시스템을 설계</li>
			</ul>
			<img src="../../images/neurocomputing/ch1/1.PNG" width={'500'} alt=''/><br/>

			<hr className='class-1'/>
			<h3 className='h5style'><b><u>1.2. 딥러닝의 응용</u></b></h3>	

			<p>기계 학습 애플리케이션은 일반적으로 세 가지 주요 분기로 나뉩니다.</p>
			<ul>
				<li><b>지도 학습</b> : 프로그램은 사전 정의된 훈련 예제 세트에 대해 훈련되고 새로운 데이터가 제공될 때 올바른 예측을 하는 데 사용됩니다.</li>
				<li><b>비지도 학습</b> : 프로그램에는 많은 데이터가 제공되며 그 안에서 패턴과 관계를 찾아야 합니다.</li>
				<li><b>강화 학습</b> : 프로그램은 행동을 생성하고 보상을 받음으로써 환경을 탐색합니다.</li>
			</ul>
			<img src="../../images/neurocomputing/ch1/2.PNG" width={'700'} alt=''/><br/>
			<p>딥러닝은 최근 머신러닝에 혁명을 일으켰습니다.</p>
			<img src="../../images/neurocomputing/ch1/3.PNG" width={'400'} alt=''/><br/>
			<p></p>

			<p></p>

		</>
	);
}	
