import React from 'react';


function SpeciselDescription() {
	return (
    <>
      <div>
          <Contents />
      </div>
    </>
	);
};

export default SpeciselDescription;

function Contents() {
	const title = 'The Specisel Description Spreadsheet';

	return (
		<>
			<h1>{title}</h1>	

			<hr className='class-1'/>		
			
		</>
	);
}	
