import React from 'react';


function ThermDescription() {
	return (
    <>
      <div>
          <Contents />
      </div>
    </>
	);
};

export default ThermDescription;

function Contents() {
	const title = 'The Therm Description Spreadsheet';

	return (
		<>
			<h1>{title}</h1>	

			<hr className='class-1'/>


		</>
	);
}	
